import AbstractView from '../../abstract-view';

export default class RulesView extends AbstractView {
  get template() {
    return `
            <div class='rules'>
              <h1 class='rules__title'></h1>
              <p class='rules__description'> 10      <img
                src='img/photo_icon.png' width='16' height='16'>   <img
                src='img/paint_icon.png' width='16' height='16' alt=''>.<br>
                      .<br>
                    30 .<br>
                    3 .<br>
                <br>
                ?
              </p>
              <form class='rules__form'>
                <input class='rules__input' type='text' placeholder=' '>
                <button class='rules__button  continue' type='submit' disabled>Go!</button>
              </form>
            </div>`;
  }

  bind() {
    const form = this.element.querySelector(`.rules__form`);
    const inputField = form.querySelector(`.rules__input`);
    const submitButton = form.querySelector(`.rules__button`);

    const onTypeText = (e) => {
      const {value} = e.target;

      if (value.length) {
        submitButton.disabled = false;
      } else {
        submitButton.disabled = true;
      }
    };

    inputField.addEventListener(`input`, onTypeText);
    form.addEventListener(`submit`, (e) => {
      e.preventDefault();
      this.goNext(inputField.value);
    });
  }

  goNext() {

  }
}
